<template>
  <div class="viSearchForm">
    <div class="form-box">
      <Form
        ref="form"
        :model="modelValue"
        @keydown.enter.prevent="search"
        :label-width="labelWidth"
        inline
      >
        <slot></slot>
      </Form>
    </div>
    <div class="btn-box">
      <slot name="operate">
        <Button @click="reset">重置</Button>
        <Button type="primary" icon="ios-search" @click="search">检索</Button>
        <slot name="extral"></slot>
      </slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "viSearchForm",
  props: {
    modelValue: {
      type: Object,
      default() {
        return {};
      }
    },
    labelWidth: {
      type: Number,
      default: 80
    }
  },
  model: {
    prop: "modelValue",
    event: "change",
  },
  data() {
    return {

    };
  },
  methods: {
    reset() {
      this.$refs.form.resetFields();
      this.search();
    },
    search() {
      this.$emit("search");
    }
  }
};
</script>

<style lang="less" scoped>
.viSearchForm {
  padding: 16px 16px 0;
  display: flex;
  .form-box {
    flex: 1;
    display: flex;
  }
  .btn-box {
    button + button {
      margin-left: 10px;
    }
  }
  :deep(.ivu-form-item) {
    margin-bottom: 10px;
  }
}
</style>
